import React, {useState} from "react";
import { createStage, checkCollision } from "../gameHelpers";
import { StyledTetrisWrapper, StyledTetris } from "./styles/StyledTetris";


// import {useInteral} from '../hooks/useInteral';
import { usePlayer } from "../hooks/usePlayer";
import { useStage } from './../hooks/useStage';
import {useGameStatus} from '../hooks/useGameStatus';

import Stage from "./Stage";
import Display  from "./Display";
import StartBtn from "./StartBtn";
// import playerRotate
import { useInterval } from '../hooks/useInterval';

import musci1 from '../../src/sounds/GameA.mp3';
import sound1 from '../../src/sounds/battle-city-sfx-6.mp3';
import sound2 from '../../src/sounds/sfx-3.mp3';

const Tetris = () => {
  const [dropTime, setDropTime] = useState(null);
  const [gameOver, setGameOver] = useState(false);

  const [player, updatePlayerPos, resetPlayer, playerRotate] = usePlayer();
  const [stage, setStage, rowsCleared] = useStage(player, resetPlayer);
  const [score, setScore, rows, setRows, level, setLevel] = useGameStatus(    rowsCleared  );

  let music = new Audio(musci1);
  let audio1 = new Audio(sound1);
  let audio2 = new Audio(sound2);
 
  // const [audio] = useState(new Audio(url));

 


  console.log('re-render');
  
    const movePlayer = dir => {
      if (!checkCollision(player, stage, { x: dir, y: 0 })) {
        updatePlayerPos({ x: dir, y: 0 });
      }
    }
  
    const startGame = () => {
      // console.log("test")
      // const audio = '/public/sounds/GameA.mp3';
      
      music.play();
      music.loop = true;
      setStage(createStage());
      setDropTime(1000);
      resetPlayer();
      setScore(0);
      setRows(0);
      setLevel(0);
      setGameOver(false);
     
    }
  
    const drop = () => {
      // Increase level when player has cleared 10 rows
      if (rows > (level + 1) * 10) {
        setLevel(prev => prev + 1);
        // Also increase speed
        setDropTime(1000 / (level + 1) + 200);
      }
  
      if (!checkCollision(player, stage, { x: 0, y: 1 })) {
        updatePlayerPos({ x: 0, y: 1, collided: false });
      } else {
        // Game over!
        if (player.pos.y < 1) {
          console.log('GAME OVER!!!');
          music.stop();
          setGameOver(true);
          setDropTime(null);
        }
        updatePlayerPos({ x: 0, y: 0, collided: true });
      }
    };
  
    const keyUp = ({ keyCode }) => {
      

      if (!gameOver) {
      
        // Activate the interval again when user releases down arrow.
        if (keyCode === 40) {
          setDropTime(1000 / (level + 1) + 200);
        }
        // audio1.pause();
        // audio1.play();
      }
    };

    const dropPlayer = () => {
      console.log('interval off')
      setDropTime(null);

      drop();
    }
  
    const move = ({ keyCode }) => {
      if (!gameOver) {
        if (keyCode === 37) {
          movePlayer(-1);
        } else if (keyCode === 39) {
          movePlayer(1);
        } else if (keyCode === 40) {
          dropPlayer();
        } else if (keyCode === 38) {
          playerRotate(stage, 1)
        }
      }
    }

    useInterval(()=>{
      drop();
    }, dropTime);
  
    return (
      <StyledTetrisWrapper role="button" tabIndex="0" onKeyDown={(e) => move(e)} onKeyUp={keyUp}>
        <StyledTetris>
          <Stage stage={stage} />
          <aside>
            {gameOver ? (
              <Display gameOver={gameOver} text="Game Over" />
            ) : (
              <div>
                <Display text={`Score: ${score}`} />
                <Display text={`Rows: ${rows}`} />
                <Display text={`Level: ${level}`} />
              </div>
            )}
            <StartBtn callback={startGame} />
          </aside>
        </StyledTetris>
      </StyledTetrisWrapper>
    );
  };
  
  export default Tetris;